html{
    scroll-behavior: smooth;
}

body{
padding: 0;
margin: 0;
}

img{
width: 100%;
height: auto;
}


*,
*::before,
*::after {
    box-sizing: border-box;
}

a,
p,
h1,
h2,
h3,
h4,
ul,
li {
    margin: 0;
    padding: 0;
} 

a{
text-decoration: none;
cursor: pointer;
}

.mainThumb{
    background-color: #343434;
    padding-top: 62px;
    padding-bottom: 72px;
    padding-left: 20px;
    padding-right: 20px;

    @media (min-width:1140px) {
        padding-top: 113px;
        
    }
}

.innerThumb{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    @media (min-width:1140px) {
        flex-direction: row;
        gap: 150px;
    }
}

.title{
    color: #fff;
font-family: "Montserrat",sans-serif;
font-size: 34px;
font-style: normal;
font-weight: 700;
line-height: 48px; /* 141.176% */
letter-spacing: -1.2px;
}

.text{
    color: #fff;
    font-family: "Montserrat",sans-serif;
    font-size: 24px;
font-style: normal;
font-weight: 400;
line-height: normal;
letter-spacing: -0.2px;
max-width: 430px;
}

.mapThumb{
    position: relative;
    width: 335px;
height: 289px;
flex-shrink: 0;
padding-left: 21px;
padding-right: 21px;
padding-top: 45px;
margin-top: 97px;
background: var(--white, #FFF);
box-shadow: 0px 100px 80px 0px rgba(0, 0, 0, 0.02), 0px 64.815px 46.852px 0px rgba(0, 0, 0, 0.02), 0px 38.519px 25.481px 0px rgba(0, 0, 0, 0.01), 0px 20px 13px 0px rgba(0, 0, 0, 0.01), 0px 8.148px 6.519px 0px rgba(0, 0, 0, 0.01), 0px 1.852px 3.148px 0px rgba(0, 0, 0, 0.00);

@media (min-width:1140px) {
    margin-top: 0;
    width: 498px;
    height: 300px;
}
}

.whiteThumbText{
    color: var(--Color-B-1, #444);
    font-family: "Montserrat",sans-serif;
font-size: 16px;
font-style: normal;
font-weight: 500;
line-height: 32px; /* 200% */
}

.whiteThumbTextUndertext{
    color: var(--Primary-B, #222);
    font-family: "Montserrat",sans-serif;
font-size: 18px;
font-style: normal;
font-weight: 600;
line-height: normal;
}

.user{
    position: absolute;
    width: 68px;
    border-radius: 50%;
    top: -30px;
    left: -20px;
}

.mapIframe{
    position: absolute;
    top: 0;
    left: 0;
    border: none;
    width: 335px;
    height: 300px;

    @media (min-width:1140px) {
        width: 498px;
        height: 300px;
    }
}

.mapTextThumb{
    position: absolute;
    background: #FFF;
    box-shadow: 0px 1px 4px 0px rgba(0, 0, 0, 0.25);    width: 203px;
height: 84px;
padding: 10px;
}

.mapText{
    color: #1E1E1E;
font-family: "Montserrat",sans-serif;
font-size: 16px;
font-style: normal;
font-weight: 700;
line-height: 134.8%; /* 21.568px */
}