html{
    scroll-behavior: smooth;
}

body{
padding: 0;
margin: 0;
}

img{
width: 100%;
height: auto;
}


*,
*::before,
*::after {
    box-sizing: border-box;
}

a,
p,
h1,
h2,
h3,
h4,
ul,
li {
    margin: 0;
    padding: 0;
} 

a{
text-decoration: none;
cursor: pointer;
}

.headerWeb{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 1140px;
    padding-top: 7px;
    padding-bottom: 23px;
}

.navigationThumb{
    display: flex;
    align-items: center;
    justify-content: center;
    gap:75px

}

.cart{
    width: 30px;
    cursor: pointer;
    margin-right: 20px;

    &:hover {
        transform: scale(1.1);
        transition: transform 0.3s ease;
    }
}

.logo{
    width: 54px;
    cursor: pointer;

    &:hover {
        transform: scale(1.1);
        transition: transform 0.3s ease;
    }
}

.navLink{
    color: #2B1E18;
font-family: "Poppins", sans-serif;
font-size: 16px;
font-style: normal;
font-weight: 600;
line-height: normal;
cursor: pointer;

&:hover {
    transform: scale(1.1);
    transition: transform 0.3s ease;
}
}

.cartThumb{
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
}

.qantityThumb{
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    position: absolute;
    top: -10px;
    right: 10px;
    background-color: #42D76A;
    width: 20px;
    height: 20px;
    border-radius: 50%;
}