html{
    scroll-behavior: smooth;
}

body{
padding: 0;
margin: 0;
}

img{
width: 100%;
height: auto;
}


*,
*::before,
*::after {
    box-sizing: border-box;
}

a,
p,
h1,
h2,
h3,
h4,
ul,
li {
    margin: 0;
    padding: 0;
} 

a{
text-decoration: none;
cursor: pointer;
}

.footer{
    padding-top: 100px;
    padding-left: 20px;
    padding-right: 20px;
}

.socialsThumb{
    display: flex;
    align-items: center;
    justify-content: flex-start;
    gap: 16px;
    margin-bottom: 60px;

    @media (min-width:1140px) {
        margin-left: 160px;
        margin-bottom: -25px;
    }
    
}

.socials{
    width: 24px;

    &:hover {
        transform: scale(1.1);
        transition: transform 0.3s ease;
    }

}

.linksThumb{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    max-width: 135px;

}

.mainLinksThumb{
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    justify-content: flex-start;
    gap: 20px;
    padding-bottom: 56px;

    @media (min-width: 1140px) {
        align-items: flex-start;
        justify-content: center;
        gap: 160px;
        padding-bottom: 60px;

    }
}

.year{
    text-align: center;
    padding-top: 20px;
    padding-bottom: 20px;
}

.mainLink{
    color: #778BC6;
font-family: "Montserrat", sans-serif;
font-size: 18px;
font-style: normal;
font-weight: 600;
line-height: normal;
}

.secondaryLink{
    color: #2B1E18;
    font-family: "Montserrat", sans-serif;
    font-size: 16px;
font-style: normal;
font-weight: 300;
line-height: normal;

&:hover {
    transform: scale(1.1);
    transition: transform 0.3s ease;
}
}